* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

img, video, canvas {
  max-width: 100%; }

body {
  background: #8eaab3;
  font-size: 16px; }

.div-map {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center; }

path:hover {
  fill: #d98c44; }

.header {
  color: #435878;
  text-shadow: 2px 2px #1d2930;
  font-size: 32px;
  padding: 10px;
  margin: auto;
  text-align: center; }

.land {
  fill: #CCCCCC;
  fill-opacity: 1;
  stroke: white;
  stroke-opacity: 1;
  stroke-width: 0.5; }

.color {
  fill: #435878; }

.color-mouse {
  fill: #d802f2; }

.input {
  margin: 10px;
  padding: 15px;
  color: #fff;
  background: #435878;
  border-radius: 5px;
  font-size: 16px; }

.form {
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 30px; }

.table-fill {
  background: white;
  border-radius: 3px;
  border-collapse: collapse;
  margin: auto;
  margin-bottom: 20px;
  max-width: 600px;
  padding: 5px;
  width: 100%;
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
          box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  -webkit-animation: float 5s infinite;
          animation: float 5s infinite; }

th {
  color: #D5DDE5;
  background: #435878;
  border-bottom: 4px solid #9ea7af;
  border-right: 1px solid #343a45;
  font-size: 23px;
  font-weight: 100;
  padding: 24px;
  text-align: left;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  vertical-align: middle; }

th:first-child {
  border-top-left-radius: 3px; }

th:last-child {
  border-top-right-radius: 3px;
  border-right: none; }

tr {
  border-top: 1px solid #C1C3D1;
  border-bottom-: 1px solid #C1C3D1;
  color: #666B85;
  font-size: 16px;
  font-weight: normal;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.1); }

tr:hover td {
  background: #4E5066;
  color: #FFFFFF;
  border-top: 1px solid #22262e;
  border-bottom: 1px solid #22262e; }

tr:first-child {
  border-top: none; }

tr:last-child {
  border-bottom: none; }

tr:nth-child(odd) td {
  background: #EBEBEB; }

tr:nth-child(odd):hover td {
  background: #4E5066; }

.last-row:hover {
  background: #4E5066;
  color: #FFFFFF;
  border-top: 1px solid #22262e;
  border-bottom: 1px solid #22262e; }

tr:last-child td:first-child {
  border-bottom-left-radius: 3px; }

tr:last-child td:last-child {
  border-bottom-right-radius: 3px; }

td {
  background: #FFFFFF;
  padding: 10px;
  text-align: left;
  vertical-align: middle;
  font-weight: 300;
  font-size: 18px;
  text-shadow: -1px -1px 1px rgba(0, 0, 0, 0.1);
  border-right: 1px solid #C1C3D1; }

td:last-child {
  border-right: 0px; }

th.text-left {
  text-align: left; }

td.text-left {
  text-align: left; }

.text-center {
  text-align: center; }

.example {
  margin-left: 0 auto; }



